import React, {useState } from "react";

function ComplaintCard ( {complaintsObj, deleteComplaintRequest, updateComplaint } ) {
    console.log('complaintObjTitle ', complaintsObj.title)


  return (
    <div class="card">

        <div class="card-body">
            <h5 class="card-title">Title: {complaintsObj.title}</h5>
            <p class="card-text">{complaintsObj.desc}</p>
            <p class="card-text">{complaintsObj.date_observed}</p>
       
        <button class="btn btn-primary"
            onClick={ 
                () => {deleteComplaintRequest(complaintsObj.id)} }>Delete</button>
        &nbsp;
        <button class="btn btn-primary" 
            onClick={ 
                () => {updateComplaint(complaintsObj.id)} }>Edit</button>
       </div>

    </div>

  );
}


export default ComplaintCard;